html {
  overflow-x: hidden; }

body {
  font-family: 'Raleway', sans-serif;
  overflow-x: hidden; }

.name-row div {
  display: inline-block; }
  .name-row div h1 {
    font-size: 83px;
    display: inline-block;
    font-weight: 300; }
  .name-row div li {
    list-style-type: none;
    text-align: left; }
  .name-row div ul {
    padding-left: 10px; }
  .name-row div a {
    color: black; }
    .name-row div a:hover {
      color: #A2698D; }

.name-ul {
  vertical-align: super; }

.name-last-name {
  color: #d179a3 !important; }

.name-row {
  text-align: center;
  padding: 10% 0; }

.jumbotron {
  height: 700px;
  margin-bottom: 0;
  background-color: white; }

.bio-header {
  height: 400px;
  background-color: #4b4b4b; }
  .bio-header h1 {
    font-size: 70px;
    text-align: center;
    padding: 8% 0;
    color: #d179a3;
    letter-spacing: 50px; }

.bio-pics {
  padding-top: 60px;
  text-align: center; }
  .bio-pics img {
    padding-left: 25px;
    padding-bottom: 80px;
    height: 300px;
    width: 300px;
    -webkit-filter: grayscale(100%);
    filter: grayscale(45%); }
    .bio-pics img:hover {
      -webkit-filter: grayscale(0%);
      filter: grayscale(0%); }

.bio-content h4 {
  display: inline-block;
  font-weight: bold; }

.bio-content .skills-list {
  display: inline-block; }

.bio-text {
  padding-bottom: 80px; }

.work-header {
  height: 400px;
  background-color: #4b4b4b; }
  .work-header h1 {
    font-size: 70px;
    text-align: center;
    padding: 7% 0;
    color: #d179a3;
    letter-spacing: 50px; }

.work-content {
  text-align: center;
  padding-bottom: 80px; }
  .work-content img {
    margin-left: 25px;
    margin-bottom: 20px;
    margin-top: 60px;
    text-align: center;
    height: 14em;
    width: 18em;
    border: 2px solid black;
    border-radius: 10px; }

.public-engagement-header {
  height: 400px;
  background-color: #4b4b4b; }
  .public-engagement-header h1 {
    font-size: 70px;
    text-align: center;
    padding: 7% 0;
    color: #d179a3;
    letter-spacing: 50px; }

.public-engagement-content {
  padding-bottom: 80px; }
  .public-engagement-content h3 {
    color: black; }
  .public-engagement-content table {
    margin-top: 3em; }
    .public-engagement-content table td, .public-engagement-content table th {
      padding: 1em; }

.contact-header {
  height: 400px;
  background-color: #4b4b4b;
  text-align: center; }
  .contact-header h1 {
    font-size: 70px;
    text-align: center;
    padding: 7% 0;
    color: #d179a3;
    letter-spacing: 50px; }

.contact-content {
  padding-bottom: 80px;
  padding-top: 40px; }
  .contact-content a {
    color: black; }
    .contact-content a:hover {
      color: #A2698D; }
  .contact-content .email-address-div {
    text-align: center; }
  .contact-content .social-media-div {
    margin-top: 3em; }
    .contact-content .social-media-div a {
      display: inline-block;
      width: 32%; }
      .contact-content .social-media-div a h3 {
        display: inline-block;
        margin-left: .5em; }
      .contact-content .social-media-div a i {
        color: #d179a3; }
  .contact-content img {
    height: 14em;
    width: 14em;
    -webkit-filter: grayscale(100%);
    filter: grayscale(45%);
    border-radius: 10px; }
    .contact-content img:hover {
      -webkit-filter: grayscale(0%);
      filter: grayscale(0%); }

@media (max-width: 768px) {
  .jumbotron {
    height: 400px; }
  .name-row div {
    height: inherit; }
    .name-row div h1 {
      font-size: 63px; }
  .bio-header {
    height: 200px;
    text-align: center; }
    .bio-header h1 {
      letter-spacing: 12px; }
  .bio-content .bio-pics img {
    padding-left: 0px;
    height: 220px;
    width: 220px;
    margin-bottom: 80px;
    border-radius: 10px;
    padding-bottom: 0px; }
  .work-header {
    height: 200px;
    text-align: center; }
    .work-header h1 {
      letter-spacing: 12px; }
  .work-content img {
    padding-left: 0px;
    height: 200px;
    width: 200px;
    margin-left: 0px; }
  .public-engagement-header {
    height: inherit; }
    .public-engagement-header h1 {
      letter-spacing: 0px;
      font-size: 40px; }
  .public-engagement-content table.speaking-engagement-table tbody tr th:nth-child(3) {
    display: none; }
  .public-engagement-content table.speaking-engagement-table tbody tr td:nth-child(3) {
    display: none; }
  .public-engagement-content table.volunteer-table tbody tr th:nth-child(3) {
    display: none; }
  .public-engagement-content table.volunteer-table tbody tr td:nth-child(3) {
    display: none; }
  .contact-header {
    height: 150px; }
    .contact-header h1 {
      font-size: 40px;
      letter-spacing: 0px; }
  .contact-content {
    text-align: center;
    padding-top: 1em; }
    .contact-content .col-md-offset-1 img:nth-child(1) {
      display: none; }
    .contact-content .contact-info .social-media-div {
      margin-top: 1em; }
    .contact-content .mobile-padding-top {
      padding-top: 2em; }
  .contact-info h3 {
    font-size: 17px; }
  .contact-content .social-media-div a h3 {
    display: none; } }
